@import "colors";

.pdb-btn {
  background-color: transparent;
  font-size: 1em;
  border-radius: 0.2em;
  cursor: pointer;
  text-transform: uppercase;
  padding: .375rem .75rem;
  display: block;
  color: $white;
  border: solid 2px;
  text-align: center;
  line-height: 1.5;
  font-weight: 600;
  vertical-align: middle;
  transition: all 0.1s ease;
  font-family: 'rawline', sans-serif;
  &:hover {
    opacity: 0.7;
  }
  &.btn-orange {
    border-color: $orange;
    background-color: $orange;
  }
  &.btn-black {
    border-color: $black;
    background-color: $black;
  }
  &.btn-clear {
    border-color: $dark;
    color: $dark;
  }
  &.btn-wide {
    width: 100%;
  }
  &:disabled {
    background-color: $lighten;
    border-color: $lighten;
    color: $gray;
    cursor: not-allowed;
    opacity: 0.8;
    &:hover {
      opacity: 1;
    }
  }
  .icon {
    margin-right: 7px;
    //font-size: 1.25em;
    vertical-align: -1px;
  }
}
